.header{
    z-index: 100;
    position: fixed;
    /* outline: 1px solid rgb(114, 67, 23); */
    width: 100%;
    height: 50px;
    box-shadow: 0 0 10px 0 #585454;
    
    background-color: rgba(0, 0, 0, 0.1);
    background-color: transparent;
    /* backdrop-filter: blur(30px); */
    overflow: hidden;
}
.wrap{
   width: 1100px; 
   margin-left: auto;
   margin-right: auto;
   display: flex;
   justify-content: space-between;
}
.logo{
    width: 156px;
    margin: -20px;
    height: 95px;
}
.burgerBtn{
    background-color: transparent;
    display: flex;
    align-items: center;
    padding: 0;
    width: 50px;
    height: 50px;
    cursor: pointer;
    border: none;
}
.burger{
    fill: white;
    width: 40px;
    height: 40px;
}
.socialsList{
    position: relative;
    display: flex;
    align-items: center;
    
    z-index: 25;
}

.socialsItem{
    margin-right: 50px;
}
.socialsItem:last-child{
    margin-right: 0;
}
.socialsLink{

}
.socialsIcon{
    fill: white;
    width: 20px;
    height: 20px;
    
    z-index: 100;
    /* fill: red; */
}